import usePoolsStore from '@/store/pool'
import { Block } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { CheckBox, Icon, RefreshButton } from '@cetus/ui-kit'
import { HStack, Switch, Text } from '@chakra-ui/react'
import SearchInputBlock from './SearchInputBlock'
interface FilterProps {
  handleIsDisplayChecked?: (event: React.ChangeEvent<HTMLInputElement>) => void
  handleIsWatchList?: () => void
  onClickIncentiveTypes?: (event: React.ChangeEvent<HTMLInputElement>) => void
  handleRefresh: () => void
  selectCoinList: Token[]
  onClickSelectCoinList: (tokenInfo: Token) => void
  onDeleteSelectCoinList: (tokenInfo: Token) => void
  isPools?: boolean
  isRefreshed?: boolean
  isProfile?: boolean
}

function PoolsFilter({
  handleIsWatchList = () => {},
  handleIsDisplayChecked = () => {},
  onClickIncentiveTypes,
  handleRefresh,
  selectCoinList,
  onClickSelectCoinList,
  onDeleteSelectCoinList,
  isPools = true,
  isProfile = false,
  isRefreshed
}: FilterProps) {
  const { isWatchList, isAllPools, isIncentivizedOnly } = usePoolsStore()
  const { isApp, windowWidth } = useWindowWidth()
  return (
    <HStack
      w={isProfile ? (isApp ? '100%' : 'unset') : '100%'}
      justify="space-between"
      flexDirection={{
        base: 'column',
        lg: 'row'
      }}
    >
      <HStack
        w={{
          base: '100%',
          lg: isPools || isProfile ? 'unset' : '100%'
        }}
        justify="space-between"
        sx={{
          '>button': {
            '>div': {
              borderRadius: isProfile ? '8px' : '12px'
            }
          }
        }}
      >
        <SearchInputBlock
          isProfile={isProfile}
          selectCoinList={selectCoinList}
          onClickSelectCoinList={onClickSelectCoinList}
          onDeleteSelectCoinList={onDeleteSelectCoinList}
        />
        {!isProfile && (isApp || !isPools) && <RefreshButton handleRefresh={handleRefresh} isRefreshed={isPools ? isRefreshed : undefined} />}
      </HStack>
      {isPools && (
        <HStack
          w={{
            base: '100%',
            lg: 'unset'
          }}
          justify="space-between"
          gap="8px"
        >
          <Block
            w={{
              base: windowWidth <= 350 ? '48px' : '36%',
              lg: 'unset'
            }}
            borderRadius="12px"
            p={{ base: '0px 8px', lg: '0 12px' }}
            h="40px"
            onClick={handleIsWatchList}
          >
            {windowWidth <= 350 ? (
              <HStack h="38px" justify="center" align="center">
                <Icon xlinkHref="#icon-icon_star" svgFill={isWatchList ? 'primary' : 'text_paragraph'} />
              </HStack>
            ) : (
              <HStack h="100%" justify="space-between" gap={{ base: '6px', lg: '8px' }}>
                <CheckBox checked={isWatchList} onClick={() => {}} />
                <Text color={isWatchList ? 'text_caption' : 'text_paragraph'}>Watchlist</Text>
              </HStack>
            )}
          </Block>
          <HStack w={{ base: '100%', lg: 'unset' }}>
            <Block
              w={{
                base: '65%',
                lg: 'unset'
              }}
              borderRadius="12px"
              p={isApp ? '0 6px 0 8px' : '0px 12px'}
              h="40px"
            >
              <HStack h="100%" justify="space-between" gap={isApp ? '4px' : '8px'}>
                <Text color={isIncentivizedOnly ? 'text_caption' : 'text_paragraph'}>{isApp ? 'Incentivized' : 'Incentivized Only'}</Text>
                <Switch isChecked={isIncentivizedOnly} onChange={onClickIncentiveTypes} />
              </HStack>
            </Block>
            <Block
              w={{
                base: '35%',
                lg: 'unset'
              }}
              borderRadius="12px"
              p={isApp ? '0 6px 0 8px' : '0px 12px'}
              h="40px"
            >
              <HStack h="100%" justify="space-between">
                <Text color={isAllPools ? 'text_caption' : 'text_paragraph'}> {isApp ? 'All' : 'All pools'}</Text>
                <Switch isChecked={isAllPools} onChange={handleIsDisplayChecked} />
              </HStack>
            </Block>
          </HStack>
          {!isProfile && !isApp && <RefreshButton handleRefresh={handleRefresh} isRefreshed={isRefreshed} />}
        </HStack>
      )}

      {/* <TokenSelectModal
        lastSelectedToken={selectCoinList?.length > 0 ? selectCoinList[0].coin_type : ''}
        isDisableSelectTokenItem={true}
        haveImport={false}
        isOpen={isOpenModal}
        onSelectToken={(tokenInfo: Token) => {
          onClickSelectCoinList(tokenInfo)
        }}
        onClose={() => {
          setIsOpenModal(false)
        }}
      /> */}
    </HStack>
  )
}

export default PoolsFilter
