import { PoolShowInfo } from '@/components/common/CoinPairInfo'
import usePositionStore from '@/store/position'
import { PosBaseInfo } from '@/types'
import { Block } from '@cetus/design'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { Button, HStack, Stack, Text, VStack } from '@chakra-ui/react'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import PositionCurrentPrice from '../PositionCurrentPrice'
import PositionItem from './PositionItem'

function PoolItem({ poolInfo, isProfile = false }: { poolInfo: any; isProfile?: boolean }) {
  const navigate = useNavigate()
  const { posPoolsRelatedData, posApiPoolData } = usePositionStore()
  const haveFarming = posApiPoolData[poolInfo?.clmmPoolAddress]?.haveFarming
  const feeDisplay = (posPoolsRelatedData[poolInfo?.list[0]?.posId]?.displayFee || '--') + '%'
  const [isOpenExpend, setIsOpenExpend] = useState<boolean>(true)

  return (
    <VStack w="100%" bg="card_bg" p="12px" borderRadius="16px" gap={{ base: '12px', lg: '16px' }}>
      <Stack
        flexDir={{ base: 'column', lg: 'row' }}
        w="100%"
        justify="space-between"
        gap={{ base: '12px', lg: '8px' }}
        onClick={() => setIsOpenExpend(!isOpenExpend)}
      >
        <PoolShowInfo
          symbolFontWeight="500"
          symbolFontSize="16px"
          symbolEllipsesDecimals={10}
          nameEllipsesDecimals={20}
          poolInfo={{ feeDisplay, ...poolInfo }}
          haveName={true}
        />
        <HStack flexDirection={{ base: 'column', lg: 'row' }} gap={{ base: '12px', lg: '8px' }}>
          <HStack w={{ base: '100%', lg: 'unset' }} justify={{ base: 'space-between', lg: 'flex-start' }} flexWrap={{ base: 'wrap', lg: 'nowrap' }}>
            <Text color="primary_gray">Current Price</Text>
            <PositionCurrentPrice
              symbolEllipsesDecimals={10}
              posId={poolInfo?.list[0]?.posId}
              displayTokenA={poolInfo?.displayTokenA}
              displayTokenB={poolInfo?.displayTokenB}
            />
          </HStack>
          {haveFarming && (
            <HStack
              bg="primary_yellow_opacity.10"
              h="32px"
              w={{ base: '100%', lg: 'unset' }}
              justify="center"
              p="0px 12px"
              borderRadius="8px"
              onClick={(e: any) => {
                cancelBubble(e)
                navigate('/farms')
              }}
              border="1px solid"
              borderColor="transparent"
              cursor="pointer"
              sx={{
                _hover: {
                  border: '1px solid',
                  borderColor: 'primary_yellow'
                }
              }}
            >
              {/* <Image src="/images/icon_farms.png" w="20px" h="20px" /> */}
              <Text color="primary_yellow" mr="-4px">
                Farm
              </Text>
              <Icon xlinkHref="#icon-icon_list_token" w="16px" h="16px" svgFill="primary_yellow" svgHover="primary_yellow" />
            </HStack>
          )}
          <HStack w={{ base: '100%', lg: 'unset' }}>
            <Button
              onClick={(e: any) => {
                cancelBubble(e)
                navigate(`/liquidity?poolAddress=${poolInfo?.clmmPoolAddress}`)
              }}
              borderRadius="8px"
              w={{ base: '50%', lg: '148px' }}
              h="32px"
              fontSize="12px"
              fontWeight="500"
              variant="outline"
            >
              Create new position
            </Button>
            <Block
              cursor="pointer"
              w={{ base: '50%', lg: 'unset' }}
              p="4px 16px"
              h="32px"
              borderRadius="8px"
              bg="none"
              _hover={{
                svg: {
                  fill: 'text_caption'
                }
              }}
            >
              <HStack justify="center" w="100%" h="100%" gap="4px">
                <Text fontSize="12px" color="primary">
                  {poolInfo?.list?.length} {poolInfo?.list?.length > 1 ? 'Positions' : 'Position'}
                </Text>
                <Icon
                  svgW="14px"
                  svgH="14px"
                  w="14px"
                  h="14px"
                  xlinkHref="#icon-icon_arrow"
                  fontSize="12px"
                  transition="transform 0.5s"
                  transform={isOpenExpend ? 'rotate(180deg)' : 'rotate(0deg)'}
                />
              </HStack>
            </Block>
          </HStack>
        </HStack>
      </Stack>
      {isOpenExpend && (
        <VStack w="100%" maxH="680px" overflow="auto">
          {poolInfo?.list.map((item: PosBaseInfo, index: number) => {
            return <PositionItem key={index} positionInfo={item} isProfile={isProfile} />
          })}
        </VStack>
      )}
    </VStack>
  )
}

export default PoolItem
