import usePositionStore from '@/store/position'
import SingleTokenInfo from '@cetus/design/src/components/common/SingleTokenInfo'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { formatCurrency, formatNumber } from '@cetus/utils'
import { Center, HStack, Popover, PopoverBody, PopoverContent, PopoverTrigger, Portal, Text, VStack } from '@chakra-ui/react'
import { useState } from 'react'

const PendingYieldValue = ({ yieldList }: { yieldList: any }) => {
  const [isShowInfo, setIsShowInfo] = useState(false)
  const { myPosYieldValue } = usePositionStore()
  const { isApp } = useWindowWidth()
  return (
    <VStack
      gap="4px"
      p={{ base: '0', lg: '12px 0' }}
      align="flex-start"
      onClick={isApp ? () => setIsShowInfo(!isShowInfo) : () => setIsShowInfo(false)}
      onMouseEnter={() => setIsShowInfo(true)}
      onMouseLeave={() => setIsShowInfo(false)}
    >
      <Popover isLazy isOpen={isShowInfo} placement="bottom-end">
        <PopoverTrigger>
          <Center as="button">
            <Text
              cursor={Number(myPosYieldValue) <= 0 ? 'text' : 'help'}
              fontSize="20px"
              color="primary_green"
              textDecoration={Number(myPosYieldValue) <= 0 ? 'none' : 'underline dotted'}
              textUnderlineOffset="2px"
            >
              {formatCurrency(myPosYieldValue, 2)}
            </Text>
          </Center>
        </PopoverTrigger>
        {isShowInfo && Number(myPosYieldValue) > 0 && yieldList?.length > 0 && (
          <Portal>
            <PopoverContent zIndex="2" w="fit-content" maxH="200px" overflow="auto" p="4px 0">
              <PopoverBody p="0px">
                <VStack w="100%" align="flex-start" bg="bg_secondary" gap="0px">
                  {yieldList?.map((item: any) => {
                    console.log('🚀 ~ {yieldList?.map ~ item:', item)
                    return (
                      <HStack w="100%" key={item?.token?.coin_type} p="10px 12px" minW="300px" justify="space-between">
                        <SingleTokenInfo
                          token={item?.token}
                          imgBoxStyle={{ w: '20px', h: '20px' }}
                          haveName={false}
                          symbolFontSize="12px"
                          warningIcon={{ iconW: '10px', iconH: '10px' }}
                        />
                        <HStack gap="4px">
                          <Text fontSize="12px" color="text_caption">
                            {formatNumber(item?.amount, 6)}
                          </Text>
                          <Text fontSize="12px">({formatCurrency(item?.amountUSD, 2)})</Text>
                        </HStack>
                      </HStack>
                    )
                  })}
                </VStack>
              </PopoverBody>
            </PopoverContent>
          </Portal>
        )}
      </Popover>
    </VStack>
  )
}

export default PendingYieldValue
