import { PoolShowInfo } from '@/components/common/CoinPairInfo'
import usePosHelper from '@/hooks/position/usePosHelper'
import usePositionStore from '@/store/position'
import { Block } from '@cetus/design'
import { CheckBox } from '@cetus/ui-kit'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import FeesRewardsValueBlock from './list/FeesRewardsValueBlock'
import LiquidityValueBlock from './list/LiquidityValueBlock'

function ModalItem({
  posInfo,
  onClickCheckBox,
  checked,
  pageFrom,
  cursor = 'pointer'
}: {
  posInfo: any
  onClickCheckBox?: (item: any) => void
  checked?: boolean
  pageFrom?: 'lpBurnPrev' | 'lpBurnNext' | 'pendingYieldModal'
  cursor?: string
}) {
  const { getClmmPosName } = usePosHelper()
  const { posPoolsOriginalData } = usePositionStore()
  const tokenName = useMemo(() => {
    if (posInfo?.tokenName) {
      return posInfo?.tokenName
    } else {
      const position_index = posPoolsOriginalData?.[posInfo?.clmmPool]?.index
      return getClmmPosName(posInfo?.index, position_index)
    }
  }, [posInfo?.tokenName, posInfo?.index, posInfo?.clmmPool, posPoolsOriginalData])

  const { posPoolsRelatedData } = usePositionStore()
  const feeDisplay = posPoolsRelatedData[posInfo?.posId]?.displayFee + '%'
  return (
    <Block w="100%" borderRadius="8px" p="0" bg="bg_primary">
      <HStack w="100%" bg="bg_third" p="12px 16px" borderRadius="8px">
        <VStack w="100%" align="flex-start" gap="16px">
          <PoolShowInfo poolInfo={{ ...posInfo, feeDisplay }} symbolEllipsesDecimals={10} />
          <Text fontSize="12px" color="primary_gray">
            {tokenName}
          </Text>
        </VStack>
        {onClickCheckBox && <CheckBox cursor={cursor} checked={checked || false} onClick={() => onClickCheckBox(posInfo)} />}
      </HStack>
      {pageFrom == 'pendingYieldModal' ? (
        <HStack p="0px 16px" w="100%" justify="space-between" gap="4px" align="flex-start">
          <Text color="primary_gray" mb="-12px" mt="12px">
            Pending yield
          </Text>
          <FeesRewardsValueBlock posInfo={posInfo} />
        </HStack>
      ) : (
        <HStack justify="space-between" p="0 16px">
          <Text color="primary_gray">Liquidity</Text>
          <LiquidityValueBlock positionInfo={posInfo} color="primary" fontSize="14px" haveTooltip={pageFrom == 'lpBurnNext' ? false : true} />
        </HStack>
      )}
    </Block>
  )
}
export default ModalItem
